<template>
  <div class="payView">
    <mu-card>
      <mu-card-title :title="'￥'+totalMoney" :subTitle="'支付剩余时间'+curTime"/>
      <mu-list>
        <mu-sub-header>购买商品</mu-sub-header>
        <mu-list-item   :title="food.name" v-for="(food,index) in foods" :key="index">
          <mu-avatar :src="food.image" slot="leftAvatar"/>
          <span  slot="right" >{{food.count}}</span>
        </mu-list-item>
      </mu-list>
      <mu-list>
          <mu-sub-header>支付方式</mu-sub-header>
          <mu-list-item disableRipple   title="钱包">
            <mu-icon value="account_balance_wallet" color="#ed3f14" slot="left"/>
            <mu-checkbox   v-model="canPay"  slot="right" />
          </mu-list-item>
        </mu-list>  
    </mu-card>
    
    <mu-raised-button fullWidth class="pay" backgroundColor="#495060" @click="goPay">
      <span class="tint">确认支付 ￥</span>
      <span class="money">{{totalMoney}}</span>      
    </mu-raised-button>
  </div>
</template>
<script>
import Util from "@/libs/util";
import Cookies from "js-cookie";
export default {
  name: "pay",
  data() {
    return {
      totalTime: 180,
      totalMoney: null,
      canPay: false,
      curTime: null,
      timer: null, //计时器
      foods: []
    };
  },
  computed: {
    getPrice() {
      return this.$route.params.price;
    },
    getFoods() {
      return this.$route.params.foods;
    }
  },
  watch: {
    getPrice(val) {
      this.totalMoney = val;
    },
    getFoods(val) {
      this.foods = val;
    }
  },
  methods: {
    goPay() {
      if (this.canPay) {
        Util.ajax
          .post("/api/client/user/pay", {
            shopId: Cookies.get("shopId"),
            userId:Cookies.get("userId"),
            foods: this.foods,
            money: this.totalMoney,
            payWay:3,
            status:3,
          })
          .then(res => {
            if (res.data.status == 1) {
              alert('支付成功');
              this.$store.commit('reduceMoney',this.totalMoney)
              this.$router.go(-1);
            } else {
              alert(res.data.msg);
            }
          });
      } else {
        alert("选择支付方式");
      }
    },
    timerInit() {
      this.timer = setInterval(() => {
        this.totalTime--;
        if (this.totalTime > 0) {
          let t1 = Math.floor(this.totalTime / 60);
          let t2 = Math.floor(this.totalTime % 60);
          t1 = t1 < 10 ? "0" + t1 : t1;
          t2 = t2 < 10 ? "0" + t2 : t2;
          this.curTime = t1 + ":" + t2;
        } else clearInterval(this.timer);
      }, 1000);
    }
  },
  mounted() {
    //路由改变，计时器未重置
    this.timerInit();
    this.totalMoney = this.$route.params.price;
    this.foods = this.$route.params.foods;
  }
};
</script>
<style lang="scss" scoped>
// .payView {
//   display: flex;
// }
// .mu-card {
//   width: 93%;
//   margin: 10px auto;
// }
.pay {
  position: fixed;
  bottom: 0;
  height: 60px;
  .tint {
    font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  }
  .money {
    font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
    font-size: 24px;
    text-indent: 10px;
    font-weight: bold;
    margin-bottom: 5px;
  }
}
.mu-card{
  height: 100vh;
}
</style>



